<!--
 * @Descripttion: 保养任务
 * @version:
 * @Author: Lois
 -->
<template>
  <el-container>
    <el-header>
        <header-buttons
          v-if="!maintainceHistoryPage.show"
          ref="headButtons"
          @buttonClickEvent="buttonClickEvent"
          cssType="1"
        ></header-buttons>
        <header-buttons
          v-if="maintainceHistoryPage.show"
          ref="headButtonsx"
          @buttonClickEvent="buttonClickEvent"
          showTitle=true
          :titleName="'保养记录'"
          showBack=true
          cssType="2"
        ></header-buttons>
      <!-- <HeaderTitle v-if="maintainceHistoryPage.show" :flag="false">
        <div slot="head-operation" class="head-operation">
         <HeadOperationItem
            name="addDevice"
            @onOprationItem="closeMaintainceHistory()"
          >
            <i slot="operation-icon" class="icon-back"></i>
            <span slot="operation-name">{{ BTN_TEXT.RETURN }}</span>
          </HeadOperationItem>
          <HeadOperationItem
            name="showInfo"
            @onOprationItem="exportTable()"
            v-if="!maintainceHistoryPage.show"
          >
            <i slot="operation-icon" class="icon-export"></i>
            <span slot="operation-name">{{ BTN_TEXT.EXPORT }}</span>
          </HeadOperationItem> -->
          <!-- <HeadOperationItem
            name="showInfo"
            @onOprationItem="refreshTable()"
          >
            <i slot="operation-icon" class="icon-refresh"></i>
            <span slot="operation-name">{{ BTN_TEXT.REFRESH }}</span>
          </HeadOperationItem>
        </div>
      </HeaderTitle> -->
    </el-header>
    <header-search
      v-if="!maintainceHistoryPage.show"
      :show-primary-search="true"
      @refreshTableData="refreshTableData"
      cssType="1"
    >
    </header-search>
    <el-main class="view eq">
      <!-- 搜索框 -->
      <search-box
        ref="searchBox"
        v-show="isSearchBoxShow"
        @searchBtnClick="complexSearch"
        :searchList="searchList"
      ></search-box>
      <search-box
        v-show="maintainceHistoryPage.isSearchBoxShow"
        @searchBtnClick="complexSearch"
        :searchList="maintainceHistoryPage.searchList"
      ></search-box>
      <!-- main table -->
      <div class="tableShow" v-if="!maintainceHistoryPage.show">
        <div style="height: 100%; width: 20%">
           <div class="workshop-tree">
             <HeaderTitle class="header-title" :titleName="'设备列表'"></HeaderTitle>
            <ZTree
              :nodes="nodeData"
              @ztreeOnClick="handleNodeClick"
              :isExpendAll="true"
              style="overflow-y: auto;" >
            </ZTree>
           </div>
        </div>
        <div style="width: 79%">
          <div id="tabs" style="height: 40px;" >
            <ul>
              <li
                v-for="(tab, index) in tabs"
                @click="toggle(index, tab.state)"
                :class="{ active: activeTab == index }"
                :key="index"
                v-show="tab.showFalg"
              >
                {{ tab.name }}
              </li>
            </ul>
          </div>
          <VXETable
            :tableName="''"
            :height="$options.filters.getTableHeight(250)"
            :tableLoading="tableLoading"
            :pager-config="maintainData.page"
            :tableDatas="maintainData.data"
            :tableColumns="myTableColumns"
            :highlightCurrentRow="true"
            :tablePage="maintainData.page"
            @sortChange="sortChange"
            @onTablePageChange="handleMaintainTablePageChange"
            @chooseCheck="tableChooseCheck"
            @clickRows="clickRows"
            ref="maintainceTaskTable"
          >
          </VXETable>
        </div>
      </div>
      <el-row v-if="maintainceHistoryPage.show" class="fix-mg">
        <maintanceHistory
          @goBack="closeMaintainceHistory"
          :value="maintainceHistoryPage.deviceCode"
          :searchData="maintainceHistoryPage.searchData"
          :confirmMaintaince="confirmMaintaince"
        ></maintanceHistory>
      </el-row>
    </el-main>
    <!-- 确认保养窗口 -->
    <confirm-maintaince-modal
      :showDialog="ConfirmMaintainceModalData.show"
      :title="ConfirmMaintainceModalData.title"
      :cancelLabel="ConfirmMaintainceModalData.cancelLabel"
      :handleCancel="clickCancel"
      :handleConfirm="confirmMaintaince"
      :handleClose="clickCancel"
      :showUser="showUser"
      :tabs="tabs"
      :requiredData="ConfirmMaintainceModalData.data"
      :dataset="ConfirmMaintainceModalData.dataset"
      :takeTask="takeTask"
      :chargebackTask="chargebackTask"
      :reviewMaintainTask="reviewMaintainTask"
      :rejectMaintainTask="showReject"
      :revokeMaintainTask="revokeMaintainTask"
    />
    <!-- 提示框 -->
    <el-dialog
      :title="TITLE.PROMPT"
      :visible.sync="centerDialogVisible"
      width="30%"
      center
    >
      <p v-show="btnFlag !== 5" style="text-align: center; width: 100%">
        {{ warmInfo }}
      </p>
      <el-input
        v-show="btnFlag === 5"
        type="textarea"
        :rows="5"
        :placeholder="MSG.WRITE_DOWN_REJECTION"
        v-model="rejectCauese"
      >
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">{{
          BTN_TEXT.CANCEL
        }}</el-button>
        <el-button type="primary" @click="btnClick">{{
          BTN_TEXT.SAVE
        }}</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改保养时间"
      :visible.sync="maintanceDialogVisible"
      width="30%"
      center >
      <el-date-picker
        type="date"
        v-model="nextMaintaceTime"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
        class="formItem_full"
      ></el-date-picker>
      <span slot="footer" class="dialog-footer">
        <el-button @click="maintanceDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveMaintaceTime">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 自定义表宽 -->
    <!-- <el-dialog
      v-if="dialogVisibleCustomColumn"
      ref="CustomColumnDialog"
      :class="[$store.state.dialogMarginClass, 'CustomColumn']"
      :title="VARIABLE.CUSTOM_COLUMN"
      :width="'600px'"
      :visible.sync="dialogVisibleCustomColumn"
    >
      <CustomColumn
        ref="CustomColumn"
        :nowColumns="maintainData.columns"
        @saveCustomColumn="saveCustomColumn"
        @showCustomColumn="showCustomColumn"
        @resetCustomColumn="resetCustomColumn"
      ></CustomColumn>
    </el-dialog> -->

    <dialog-setting-column
        :show-setting-dialog.sync="isSettingCustomDialogShow"
        :default-columns="maintainData.columns"
        :page-columns="myTableColumns"
        @saveMyColumnSetting="handleSaveColumnSetting"
      />

    <mkMaterialModal
      ref="mkMaterialModal"
      :showDialog="showMkMaterilDialog"
      :pageType="'保养'"
      :programName="programName"
      :programNum="programNum"
      @handleMkMaterilClose="handleMkMaterilClose"
      ></mkMaterialModal>
  </el-container>
</template>

<script>
import js from '@/assets/js/equipment/MaintainceTask/index.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/equipment/maintanceTask/index.less';
</style>
