import { MaintanceTaskService } from '@/services/maintanceTask.service';
import ConfirmMaintainceModal from '@/components/modal/maintanceTaskModal/confirmMaintainceModal.vue';
import { EQUIPMENT_DATA, BTN_TEXT, PAGE_TITLE, MAINTAINTASK_STATUS, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import { insertOrUpdateMaintainTask, updateRepairTask, getPageMaintainTaskByDeviceCode, insertOrUpdateBatchMaintainItems, getMaintainTaskById, deleteByIds } from '@/api/equipment';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 引入table组件
import VXETable from '@/components/common/Table';
import {
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
import { searchCondetionFunc } from '@/utils/publicFunction';
export default {
  components: {
    VXETable,
    ConfirmMaintainceModal,
    insertOrUpdateMaintainTask
  },
  props: {
    value: { type: String, default: '' },
    searchData: { type: Array, default: () => [] },
    confirmMaintaince: Function
  },
  data() {
    return {
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      tableData: [],
      MaintainceHistoryEditModalData: {
        title: PAGE_TITLE.MAINTANCE_HISTORY_UPDATE,
        show: false,
        data: null,
        dataset: {
          isChargeback: 2,
          isSave: 2,
          isSubmitReview: 2,
          isReject: 2,
          isRevoke: 2
        }
      },
      tableLoading: false,
      historyTableConfig: {
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          layouts: [
            'Sizes',
            'PrevJump',
            'PrevPage',
            'Number',
            'NextPage',
            'NextJump',
            'FullJump',
            'Total'
          ]
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          {
            field: 'levelName',
            visible: true,
            title: EQUIPMENT_DATA.MAINTANCE_LEVEL,
            align: 'center'
          },
          {
            field: 'cycleName',
            visible: true,
            title: '保养周期',
            align: 'center'
          },
          {
            field: 'templateName',
            visible: true,
            title: '保养模板',
            align: 'center'
          },
          {
            field: 'maintainUserNameList',
            visible: true,
            title: '保养人',
            align: 'center'
          },
          {
            field: 'maintainTime',
            title: EQUIPMENT_DATA.MAINTAINCE_TIME_BOOK,
            align: 'center',
            sortable: true
          },
          {
            field: 'startTime',
            title: EQUIPMENT_DATA.MAINTAINCE_START_TIME,
            align: 'center',
            sortable: true
          },
          {
            field: 'endTime',
            title: EQUIPMENT_DATA.MAINTAINCE_END_TIME,
            align: 'center',
            sortable: true
          },
          {
            field: 'maintainState',
            title: EQUIPMENT_DATA.STATUS,
            align: 'center',
            sortable: true,
            slots: {
              default: ({ row }) => {
                return [<span>{MAINTAINTASK_STATUS[row.maintainState]}</span>];
              }
            }
          }
          // {
          //   title: BTN_TEXT.OPERATION,
          //   width: '180',
          //   fixed: 'right',
          //   align: 'center',
          //   slots: {
          //     default: ({ row }) => {
          //       var res = [];
          //       if (row.maintainState === 1) {
          //         res = [<el-dropdown
          //           placement="bottom"
          //           size="small"
          //           onCommand={(command) => this.handleCommand(command, row)}
          //         >
          //           <span class="operation-column" >
          //             {BTN_TEXT.OPERATION}
          //           </span>
          //           <el-dropdown-menu slot="dropdown">
          //             <el-dropdown-item class="operation-column" command="info">{BTN_TEXT.INFO}</el-dropdown-item>
          //             <el-dropdown-item class="operation-column" command="change">{BTN_TEXT.MODIFY}
          //             </el-dropdown-item>
          //           </el-dropdown-menu>
          //         </el-dropdown>];
          //       } else {
          //         res = [<el-dropdown
          //           placement="bottom"
          //           size="small"
          //           onCommand={(command) => this.handleCommand(command, row)}
          //         >
          //           <span class="operation-column" >
          //             {BTN_TEXT.OPERATION}
          //           </span>
          //           <el-dropdown-menu slot="dropdown">
          //             <el-dropdown-item class="operation-column" command="info">{BTN_TEXT.INFO}</el-dropdown-item>
          //           </el-dropdown-menu>
          //         </el-dropdown>];
          //       }

          //       return res;
          //     }
          //   }
          // }
        ]
      },
      // 只读详情还是修改详情
      disableUpdate: false,
      orderByData: {},
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: []
    };
  },
  methods: {
    buttonClickEvent(type) {
      this[`${type}`]();
    },
    initData() {
      this.getPageMaintainTaskByDeviceCodeData(this.value);
    },
    /**
   * @description: 操作下拉事件
   * @param 无
   * @return: 无
   * @author: CH
   */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.editRowEvent(row, 2);
          break;
        case 'change':
          this.editRowEvent(row, 1);
          break;
      }
    },
    // 根据设备查询该设备的保养记录
    getPageMaintainTaskByDeviceCodeData(data) {
      this.tableLoading = true;
      // let requestBody = {};
      // requestBody = {
      //   conditionJson1: this.searchData ? this.searchData.conditionArr1 : null,
      //   operator: this.searchData ? this.searchData.groupOperator : null,
      //   conditionJson2: this.searchData ? this.searchData.conditionArr2 : null,
      //   orderBy: this.orderByData
      // };

      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      getPageMaintainTaskByDeviceCode({
        deviceCode: data,
        current: this.historyTableConfig.page.currentPage,
        size: this.historyTableConfig.page.pageSize
      }, conditionJson).then(res => {
        this.tableLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.historyTableConfig.page.total = res.total;
          this.tableData = MaintanceTaskService.processMaintainData(res.rows);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 确认保养
     * @param: {Object} data 保养任务元数据
     * @return: void
     */
    saveMaintainceHistoryChange(confirmdata) {
      var data = JSON.parse(JSON.stringify(confirmdata));
      this.clickCancel();
      updateRepairTask({
        maintainId: data.maintainId,
        maintainRemark: data.maintainRemark,
        maintainState: data.maintainState,
        startTime: data.startTime,
        endTime: data.endTime,
        maintainUserIdList: data.userIdList,
        maintainImageFileIds: data.maintainImageFileIds
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS || res.code === STATUS_CODE.OPERATION_UPDATE_SUCCESS) {
          showSuccessMessage(res.message);
          // 更新扩展字段
          if (data.extendPropertyValueVoList) {
            this.saveExtendValue(data.extendPropertyValueVoList);
          } else {
            this.initData();
          }
          if (data.delFileList.length !== 0) {
            this.delImg(data.delFileList);
          }
          // 更新保养项关联的状态和实测值
          this.updateMaintainItemsRelation(data.maintainceItems);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
* @description: 删除图片
* @param {type}
* @return:
*/
    async delImg(delFileList) {
      try {
        const res = await deleteByIds({ ids: delFileList }, []);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.delFileList = [];
          // 提示正常信息
          // showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 确认保养 -更新保养项关联的状态和实测值
     * @param: {Array} data 保养项关联值数组
     * @return: void
    */
    updateMaintainItemsRelation(data) {
      var res = [];
      data.forEach(function(item) {
        var i = {
          completed: item.completed,
          relationshipId: item.relationshipId,
          value: item.value
        };
        res.push(i);
      });
      insertOrUpdateBatchMaintainItems(null, res).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 修改保养记录
     * @param: {Object} data 行记录
     * @return: void
    */
    editRowEvent(row, falg) {
      getMaintainTaskById({
        maintainId: row.maintainId
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var result = MaintanceTaskService.processMaintainData([res.rows]);
          var requiredData = result[0];
          this.MaintainceHistoryEditModalData.dataset.isSave = falg;
          requiredData.maintainRemark = requiredData.maintainRemark ? requiredData.maintainRemark : '';
          requiredData.users = requiredData.maintainUserIdList ? requiredData.maintainUserIdList : [];
          requiredData.maintaince_start_ts = requiredData.startTime;
          requiredData.maintaince_end_ts = requiredData.endTime;
          this.MaintainceHistoryEditModalData.data = requiredData;
          this.MaintainceHistoryEditModalData.show = true;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    // 显示详情
    showConfirmMaintainceModal() {
      var row = {};
      if (!this.$refs.maintainceTaskTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.maintainceTaskTable.getRadioRecord();
      }
      this.MaintainceHistoryEditModalData.show = true;
      this.MaintainceHistoryEditModalData.data = row;
      this.disableUpdate = true;
    },
    clickCancel: function() {
      this.disableUpdate = false;
      this.MaintainceHistoryEditModalData.show = false;
    },
    goBack(data) {
      var res = {
        page: 'maintanceHistory',
        data: data
      };
      this.$emit('goBack', res);
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     */
    saveExtendValue(data) {
      var arr = data.map((item) => {
        if (item.extendValue === null) {
          item.extendValue = '';
        }
        return item;
      });
      extendPropertyValueInsertOrUpdateBatchAction({}, arr)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.initData();
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    handleHistoryTablePageChange(currentPage, pageSize) {
      this.historyTableConfig.page.currentPage = currentPage;
      this.historyTableConfig.page.pageSize = pageSize;
      this.getPageMaintainTaskByDeviceCodeData(this.value);
    },
    // 表格排序
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      if (this.active === 0) {
        this.handleHistoryTablePageChange(1, this.historyTableConfig.page.pageSize);
      } else {
        this.handleHistoryTablePageChange(1, this.historyTableConfig.page.pageSize);
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.historyTableConfig.page.currentPage = 1;
      }
      this.initData();
    }
  },
  watch: {
    value(val, oldVal) {
      this.initData();
    },
    searchData(val, oldVal) {
      this.initData();
    }
  },
  mounted() {
    this.initData();
  }
};
