      switch (command) {
        case 'takeTask':
          this.warmInfo = EQUIPMENT_DATA.RECEIVE_THE_ORDER;
          this.centerDialogVisible = true;
          this.taskData = row;
          this.btnFlag = 1;
          // this.takeTask(row);
          break;
        case 'chargeback':
          this.warmInfo = EQUIPMENT_DATA.CHARGEBACK_THE_ORDER;
          this.centerDialogVisible = true;
          this.taskData = row;
          this.btnFlag = 2;
          // this.takeTask(row);
          break;
        case 'revoke':
          this.warmInfo = EQUIPMENT_DATA.REVOKE_THE_ORDER;
          this.centerDialogVisible = true;
          this.taskData = row;
          this.btnFlag = 3;
          // this.takeTask(row);
          break;
        case 'pass':
          this.warmInfo = EQUIPMENT_DATA.PASS_THE_ORDER;
          this.centerDialogVisible = true;
          this.taskData = row;
          this.btnFlag = 4;
          // this.takeTask(row);
          break;
        case 'reject':
          this.warmInfo = EQUIPMENT_DATA.REJECT_THE_ORDER;
          this.centerDialogVisible = true;
          this.taskData = row;
          this.rejectCauese = '';
          this.btnFlag = 5;
          // this.takeTask(row);
          break;
        case 'info':
          this.showConfirmMaintainceModal(row);
          break;
        case 'history':
          this.showMaintainceHistory([row]);
          break;
        default:
          break;
      }
