    // *****************确认保养 part start*****************
    /**
     * @description: 确认保养弹窗弹出
     * @param: {Object} data 保养任务元数据
     * @return: void
     */
    showConfirmMaintainceModal() {
      var data = {};

      if (!this.$refs.maintainceTaskTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.maintainceTaskTable.getRadioRecord();
      }
      // 根据id获取保养任务详情
      getMaintainTaskById({
        maintainId: data.maintainId
      })
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            var result = MaintanceTaskService.processMaintainData([res.rows]);
            var requiredData = result[0];
            requiredData.maintainRemark = requiredData.maintainRemark
              ? requiredData.maintainRemark
              : '';
            requiredData.users = requiredData.maintainUserIdList
              ? requiredData.maintainUserIdList
              : [];
            requiredData.maintaince_start_ts = requiredData.startTime;
            requiredData.maintaince_end_ts = requiredData.endTime;
            this.ConfirmMaintainceModalData.data = requiredData;
            this.ConfirmMaintainceModalData.dataset = res.dataset;
            this.ConfirmMaintainceModalData.show = true;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 确认保养
     * @param: {Object} data 保养任务元数据
     * @return: void
     */
    confirmMaintaince(confirmdata) {
      var data = JSON.parse(JSON.stringify(confirmdata));
      var url = null;
      if (data.flag === 1) {
        url = submitMaintainTask;
      } else {
        url = updateRepairTask;
      }
      this.clickCancel();
      url({
        maintainId: data.maintainId,
        maintainRemark: data.maintainRemark,
        maintainState: data.maintainState,
        startTime: data.startTime,
        endTime: data.endTime,
        maintainUserIdList: data.userIdList,
        maintainImageFileIds: data.maintainImageFileIds
      })
        .then((res) => {
          if (
            res.code === STATUS_CODE.OPERATION_SUCCESS ||
            res.code === STATUS_CODE.OPERATION_UPDATE_SUCCESS
          ) {
            // 更新扩展字段
            if (data.extendPropertyValueVoList) {
              this.saveExtendValue(data.extendPropertyValueVoList);
            } else {
              this.getMaintainData();
            }
            if (data.delFileList.length !== 0) {
              this.delImg(data.delFileList);
            }
            // 更新保养项关联的状态和实测值
            if (data.maintainceItems.length > 0) {
              this.updateMaintainItemsRelation(data.maintainceItems);
            }
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
